body {
    margin: 0;
    font-family: IRANSans;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
}
@font-face {
    font-family: IRANSans;
    font-style: normal;
    font-weight: 300;
    src: url("./fonts/IRANSansWeb.eot");
    src: url("./fonts/IRANSansWeb.eot?#iefix") format("embedded-opentype");
    src: url("./fonts/IRANSansWeb.woff2") format("woff2");
    src: url("./fonts/IRANSansWeb.woff") format("woff");
    src: url("./fonts/IRANSansMobile(FaNum).ttf") format('truetype');
    /* font-display: fallback */
}
#root,
.container {
    width: 1600px;
    margin: auto;
    height: 100%;
}
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
::placeholder {
    font-family: IRANSans;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.main {
    margin-top: 50px;
}
.navbar {
    height: 50px;
    padding: 10px !important;
}
header {
    position: fixed;
    width: auto;
    height: 50px;
    top: 0;
    right: 0;
    left: 0;
    z-index: 555555 !important;
    background-color: #e43535;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    text-align: right;
}
header .container {
    position: relative;
    background-color: #e43535;

}
header .container svg {
    margin-top: 16px;
    cursor: pointer;
    margin-right: 60px;
    z-index: 5;
    float: right;
}
footer {
    position: fixed;
    height: 40px;
    background-color: grey;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
}
.searchField {
    display: block;
    width: calc(100% - 700px);
    height: 30px;
    color: white;
    border: 1px solid white;
    background-color: #e43535;
    outline: unset;
    padding: 0 10px 4px;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.menu-icon {
    position: absolute;
    top: 13px;
    right: 5px;
    z-index: 2;
    cursor: pointer;
    position: absolute;
    display: block;
    margin: 0;
}
.menu-icon div {
    width: 40px;
    height: 3px;
    background: #fff;
    margin: 4px;
    -webkit-transition: 0.35s ease-in-out;
    transition: 0.35s ease-in-out;
}

.menu-icon.active .line_one {
    transform: translateY(7px) translateX(0) rotate(-45deg);
    -webkit-transform: translateY(7px) translateX(0) rotate(-45deg);
    -moz-transform: translateY(7px) translateX(0) rotate(-45deg);
}
.menu-icon.active .line_two {
    opacity: 0;
}
.menu-icon.active .line_three {
    transform: translateY(-7px) translateX(0) rotate(45deg);
    -webkit-transform: translateY(-7px) translateX(0) rotate(45deg);
    -moz-transform: translateY(-7px) translateX(0) rotate(45deg);
}

.second-level {
    position: absolute;
    z-index: -2;
    width: auto;
    height: 481px;
    bottom: 0;
    -webkit-box-shadow: 0 0.4px 3px #b5b1b1;
    box-shadow: 0 0.4px 3px #b5b1b1;
    visibility: hidden;
    background-color: white;
    -webkit-transition: 0.4s ease-in-out 150ms;
    transition: 0.4s ease-in-out 150ms;
    left: 2px;
    right: 2px;
}
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.second-level.openMenu {
    visibility: visible;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
}
.catLi {
    width: 200px;
    height: 30px;
    font-size: 14px;
    margin-top: 1px;
    padding: 5px 20px 0 0;
    cursor: default;
}
.cat ul {
    position: absolute;
    top: 32px;
    right:200px;
    width: calc(100% - 200px);
    background-color: white;
    display: none;

}
.catLi:hover{
    background-color: #e43535;
    color: white;
}
.catLi:hover ul {
    display: block;
    bottom: 0;
}
.cat ul li{
    width: calc(100% / 5);
    height: 30px;
    color: black;
        padding: 7px 15px 5px;
        float: right;
        position: relative;
}
.cat ul li::before{
    content: "";
    width: 5px;
    height: 5px;
    background-color: #e43535;
    position: absolute;
    right: 5px;
        top: 0;
    bottom: 0;
    margin: auto;
}